#footer {
    background-color: #17171a;
    color: white;
    line-height: 32px;
    font-size: 13px;
    font-family: 'Roboto';
    border-top: 8px solid #7c284e;
    margin-top: 20px;
    a {
        color: white;
        text-decoration: none;
        margin: 0 5px;
        &:hover {
            transition: all 0.3s;
            color: #7c284e;
        }
    }
    #site-info {
        max-width: 1200px;
        width: 100%;
        box-sizing: border-box;
        margin: 0 auto;
        text-align: center;
        padding: 20px;
    }
    .progress-container {
        width: 100%;
        height: 4px;
        position: fixed;
        bottom: 0;
        .progress-bar {
            height: 8px;
            background: #7c284e;
            width: 0%;
          }
    }
    .footer-links {
        a {
            padding: 4px;
            border-radius: 2px;
            &:first-child {
                &:hover {
                    color: white;
                    background-color: #08f;
                }
            }
            &:nth-child(2) {
                &:hover {
                    color: white;
                    background-color: #40bff5;
                }
            }
            &:nth-child(3) {
                &:hover {
                    color: white;
                    background-color: #e13138;
                }
            }
            &:last-child {
                &:hover {
                    color: white;
                    background-color: #c13584;
                }
            }
        }
    }
    @media (max-width: 767px) {
        .footer-links {
            margin-top: 15px;
            a {
                font-size: 18px;
            }
        }
    }
}